/*стили для разработки*/
body {
  margin: 0;
  padding: 0;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 940px;
  padding: 0 10px;
  margin: 0 auto;
}
.header-panel-top {
  display: block;
  margin: 0;
  padding: 0;
  background: #29344a;
}
.header-panel-top .header-logo {
  display: block;
  background: #ea3544;
  color: #fff;
  font-size: 24px;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 55px;
  padding-bottom: 5px;
  margin: 0;
  -webkit-box-shadow: 0 4px  #b62a32;
          box-shadow: 0 4px  #b62a32;
}
.header-panel-top form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 270px;
  margin: 0;
  padding: 0;
}
.header-panel-top label[for="search"] {
  display: block;
  margin: 0;
  padding: 0;
  height: 43px;
  background: transparent;
  width: 100%;
  position: relative;
}
.header-panel-top label[for="search"]::before {
  content: '';
  position: absolute;
  top: 12px;
  left: 17px;
  width: 20px;
  height: 20px;
  background: red;
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
}
.header-panel-top #search {
  margin: 0;
  border: none;
  background: transparent;
  width: 222px;
  font-family: inherit;
  font-size: 17px;
  padding-left: 48px;
  padding-top: 11px;
  padding-bottom: 13px;
  -webkit-transition:  background .5s;
  transition:  background .5s;
}
.header-panel-top #search:focus {
  background: #fff;
}
.header-panel-top #search::-webkit-input-placeholder {
  color: #fff;
}
.header-panel-top #search:-ms-input-placeholder {
  color: #fff;
}
.header-panel-top #search::-ms-input-placeholder {
  color: #fff;
}
.header-panel-top #search::placeholder {
  color: #fff;
}
.header-panel-top #search:focus::-webkit-input-placeholder {
  color: #000;
}
.header-panel-top #search:focus:-ms-input-placeholder {
  color: #000;
}
.header-panel-top #search:focus::-ms-input-placeholder {
  color: #000;
}
.header-panel-top #search:focus::placeholder {
  color: #000;
}
.header-panel-top a {
  display: block;
  padding-top: 11px;
  padding-left: 45px;
  padding-bottom: 13px;
  color: #fff;
  text-decoration: none;
}
.header-panel-top .bookmark {
  padding-right: 31px;
  font-size: 17px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAQBAMAAADUulMJAAAAAXNSR0IB2cksfwAAACFQTFRFAAAA////////////////////////////////////////zyHr+QAAAAt0Uk5TAD9MLkEQA0YXJweZB2xpAAAALklEQVR4nGMQUgIBRQYlCKAmbQyhXRlCQLRyAgObkRKDegMDAwNHEcMsBhBYCQC9Vw+tceis7wAAAABJRU5ErkJggg==);
  background-repeat: no-repeat;
  background-position: 17px 12px;
}
.header-panel-top .cart {
  padding-right: 31px;
  font-size: 17px;
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 14px 12px;
}
.header-panel-top .order {
  padding-left: 20px;
  font-size: 17px;
  padding-right: 22px;
  background: #63a63f;
}
.header-panel-middle {
  padding-top: 32px;
  background: #f3f7fa;
}
.header-panel-middle .description {
  margin-left: 53px;
  font-style: italic;
  line-height: 23px;
  color: #ea3644;
}
.header-panel-middle .phone {
  display: block;
  margin-top: 14px;
  margin-left: 70px;
  font-size: 21px;
  padding-top: 5px;
  padding-left: 70px;
  padding-right: 49px;
  padding-bottom: 8px;
  border: 4px solid #fff;
  text-decoration: none;
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 12px 8px;
  color: #ea3644;
}
.header-panel-middle .adress {
  font-size: 14px;
