  margin-left: 80px;
  color: #303c54;
}
.header-panel-middle .user-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-left: 50px;
  margin-top: 13px;
  width: 300px;
  height: 45px;
}
.header-panel-middle .enter {
  display: block;
  margin-left: 20px;
  padding-top: 10px;
  padding-left: 47px;
  padding-bottom: 12px;
  width: 75px;
  color: #000;
  font-size: 21px;
  text-decoration: none;
  background: #fff url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 14px 14px;
}
.header-panel-middle .register {
  display: block;
  float: right;
  padding-top: 10px;
  padding-right: 24px;
  padding-bottom: 12px;
  padding-left: 25px;
  background: #fff;
  color: #000;
  font-size: 21px;
  text-decoration: none;
}
.header-panel-bottom {
  background: #f3f7fa;
  padding-bottom: 59px;
}
.header-panel-bottom ul,
.haeder-panel-bottom li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-navigation {
  margin-top: 24px;
}
.main-navigation ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 940px;
  border-bottom: 5px solid #293448;
}
.main-navigation a {
  display: block;
  padding-left: 32px;
  padding-right: 25px;
  padding-top: 18px;
  padding-bottom: 17px;
  text-decoration: none;
  font-size: 18px;
  background: #32425c;
  color: #fff;
}
.main-navigation a:hover {
  background: #293448;
}
.main-navigation li:first-child a {
  padding-left: 50px;
}
.main-navigation li:last-child a {
  padding-right: 53px;
}
